import React, { useState, useEffect } from 'react';
import axios from 'axios';
import ReviewComponent from '../subComponents/ReviewComponent';
import VideoSliderComponent from '../subComponents/VideoSliderComponent';
import SortComponent from '../subComponents/SortComponent';
import ScoreComponent from '../subComponents/ScoreComponent';

const Home = () => {
  const [data, setdata] = useState([]);
  const [sort, setSort] = useState('date');
  const [visible, setVisible] = useState(6);
  const showMoreWorks = () => {
    setVisible((prevValue) => prevValue + 6);
  };

  //Fetching data from api by using id attributes passed in the render div element
  const merchantId = document.getElementById('khooobsooorat-widget').dataset
    .merchantId;
  const merchantProductId = document.getElementById('khooobsooorat-widget')
    .dataset.merchantProductid;

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(
          `https://khooobsooorat-poc-1.herokuapp.com/review/getReviewsOfProduct/${merchantId}/${merchantProductId}?page=1&size=${visible}&filterBy=${sort}`
        );
        setdata(response.data);
      } catch (err) {
        setdata(null);
        console.log(err);
      }
    };
    fetchData();
  }, [merchantId, merchantProductId, sort, visible]);

  const { highlightedRatings, paginatedReviewData, total } = data;
  // console.log('mydata', reviewData);
  // console.log('rev', typeof setTotalFilters);

  return (
    <div>
      <div className='reviews'>
        <div className='reviews__header'>
          <div className='reviews-gallery'>
            <div className='reviews-gallery__header'>
              <h4>
                Unbiased Reviews by{' '}
                <span className='ksname'>Khooobsooorat</span>
              </h4>
            </div>
            <hr className='mb-0' />
            <ScoreComponent
              reviewData={paginatedReviewData}
              featureData={highlightedRatings}
            />
            <hr className='mb-4' />
            <VideoSliderComponent data={paginatedReviewData} />
          </div>
        </div>
        <hr />
        <div className='reviews__body'>
          <SortComponent setSort={setSort} />
          <ReviewComponent reviewData={paginatedReviewData} />
        </div>
        <div className='reviews__footer'>
          <div className='read-more'>
            <span className='text me-2' onClick={showMoreWorks}>
              {total && total <= visible
                ? 'No more reviews'
                : 'Read more reviews'}
            </span>
            {total && total <= visible ? null : (
              <i className='fa fa-long-arrow-down' aria-hidden='true'></i>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Home;
