import React, { useState } from 'react';
import Rating from '../Rating';
import cx from 'classnames';
import moment from 'moment';
// import keyword_extractor from 'keyword-extractor';
import { Configuration, OpenAIApi } from 'openai';

const ReviewCardComponent = (props) => {
  const { rating, view_count, created, review, review_type, ks_customers } =
    props.cardData;
  const [expand, setExpand] = useState(false);
  const [keywords, setKeywords] = React.useState([]);
  const handleClick = () => {
    setExpand(!expand);
  };

  // Set placeholder image in case of broken image links
  const addDefaultSrc = (ev) => {
    ev.target.src = 'https://www.khooobsooorat.com/images/user-img-icon.png';
  };

  //  Extract the keywords with keyword_extractor
  // const extraction_result = keyword_extractor
  //   .extract(review, {
  //     language: 'english',
  //     remove_digits: true,
  //     return_changed_case: true,
  //     remove_duplicates: true,
  //     return_chained_words: true,
  //     return_max_ngrams: true,
  //   })
  //   .splice(0, 4);

  //  Extract the keywords with openai
  //   let array = [];
  const configuration = new Configuration({
    apiKey: process.env.REACT_APP_OPENAI_API_KEY,
  });
  const openai = new OpenAIApi(configuration);

  const myFunc = async () => {
    const response = await openai.createCompletion({
      model: 'text-davinci-002',
      prompt: `Extract 4 positive keywords from ${review} and return the keywords seperated by comma.`,
      temperature: 0,
      max_tokens: 256,
      top_p: 1,
      frequency_penalty: 0,
      presence_penalty: 0,
    });
    setKeywords(response.data.choices[0].text.split(','));
  };
  myFunc();

  //   array.push(response.data.choices[0].text);

  //   array = keywords.split(',');
  // console.log('response', keywords);

  return (
    <div className='reviews__list-item row align-items-start'>
      <div className='col-2 d-flex flex-sm-column align-items-center'>
        <img
          className='avatar '
          onError={addDefaultSrc}
          src={
            ks_customers.image_name
              ? `https://www.khooobsooorat.com/images/customers/${ks_customers.image_name}`
              : 'https://www.khooobsooorat.com/images/user-img-icon.png'
          }
          alt='IMG'
        />
        <h5 className='name'>{ks_customers.first_name}</h5>
      </div>
      <div onClick={handleClick} className='col textual'>
        <i
          className={cx('fa fa-angle-down', expand ? 'up' : null)}
          aria-hidden='true'
        ></i>
        <h4 className='mb-3'>{review_type}</h4>
        <div className='rating-wrapper d-flex'>
          <Rating rating={rating} />
          <p className='date'>{moment(created).utc().format('DD/MM/YYYY')}</p>
          <div className='views d-flex gap-2 align-items-baseline'>
            <i className='fa fa-eye' aria-hidden='true'></i>
            <p className='date'>{view_count}</p>
          </div>
          <div className='verified'>
            Verified on <span className='ksname'>Khooobsooorat</span>
          </div>
        </div>
        <div hidden={!expand} className='review-text-box'>
          {review}
        </div>
        <div className='tags d-flex'>
          {keywords.map((item, idx) => {
            return (
              <div key={idx} className='tag-item'>
                {item}
                {/* .charAt(0).toUpperCase() + item.slice(1) */}
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default ReviewCardComponent;
