import React from 'react';
import Rating from '../Rating';
import moment from 'moment';

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation } from 'swiper';
import keyword_extractor from 'keyword-extractor';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';

const VideoSliderComponent = (data) => {
  //To take review as argument and return the keyphrases
  const keyPhrase = (text) => {
    const extraction_result = keyword_extractor
      .extract(text, {
        language: 'english',
        remove_digits: true,
        return_changed_case: true,
        remove_duplicates: true,
        return_chained_words: true,
        return_max_ngrams: true,
      })
      .splice(0, 4);
    return extraction_result;
  };

  return (
    <div className='wrapper'>
      <div className='reviews-gallery__body'>
        <Swiper
          spaceBetween={10}
          slidesPerView={'auto'}
          navigation
          modules={[Navigation]}
        >
          {data.data &&
            data.data.map((item, idx) => {
              return (
                item.video_name && (
                  <SwiperSlide key={idx}>
                    <div className='reviews-gallery__item'>
                      <div className='video-frame'>
                        <div className='bottom-frame'>
                          <p>
                            <strong>{item.review_type}</strong>
                          </p>
                          <div className='rating-wrapper d-flex align-items-center'>
                            <Rating rating={item.rating} />
                            <p className='date'>
                              {moment(item.created).utc().format('DD/MM/YYYY')}
                            </p>
                            <div className='views d-flex gap-2 align-items-baseline '>
                              <i className='fa fa-eye' aria-hidden='true'></i>
                              <p>{item.view_count}</p>
                            </div>
                          </div>
                          <div className='brand-info'>
                            <strong className='ksname'>Belora Paris</strong>
                            <div>
                              <strong>Unbiased Review</strong> by:{' '}
                              <strong className='ksname'>
                                {item.ks_customers.first_name}{' '}
                                {item.ks_customers.last_name}
                              </strong>
                            </div>
                          </div>

                          <div className='video-tags'>
                            {keyPhrase(item.review).map((item, idx) => {
                              return (
                                <p key={idx} className='tag-item'>
                                  {item}
                                </p>
                              );
                            })}
                          </div>
                          <div className='bottom-bar'>
                            <p>
                              Review Featured on:{' '}
                              <a
                                target='_blank'
                                href='https://www.nykaa.com'
                                rel='noreferrer'
                              >
                                Nykaa
                              </a>
                            </p>
                          </div>
                        </div>
                        <iframe
                          width='400'
                          height='650'
                          src={item.video_name}
                          title={item.video_name}
                          allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture'
                        ></iframe>
                      </div>
                    </div>
                  </SwiperSlide>
                )
              );
            })}
        </Swiper>
      </div>
    </div>
  );
};

export default VideoSliderComponent;
