import React from 'react';
import cx from 'classnames';

const FilterComponent = () => {
  const [activeTag, setActiveTag] = React.useState([]);

  const handleClick = (id) => {
    activeTag.includes(id)
      ? setActiveTag([...activeTag.filter((item) => item !== id)])
      : setActiveTag((existingItems) => {
          return [...existingItems, id];
        });
  };
  // console.log('activeTag', activeTag);

  return (
    <div className='reviews__filter row my-5'>
      <div className='filter col '>
        {[
          'Value for money',
          'Good quality',
          'Smooth',
          'Smudge-free',
          'Pigmented',
        ].map((item, idx) => {
          return (
            <button
              key={idx}
              onClick={() => handleClick(idx)}
              className={cx('tag', activeTag.includes(idx) ? 'active' : '')}
            >
              {item}
              {activeTag.includes(idx) ? (
                <i className='fa fa-times ms-2' aria-hidden='true'></i>
              ) : null}
            </button>
          );
        })}
      </div>
    </div>
  );
};

export default FilterComponent;
