import React, { useState } from 'react';
import Rating from '../Rating';
import cx from 'classnames';
import FilterComponent from '../FIlterComponent';

const ScoreComponent = (props) => {
  const { reviewData, featureData } = props;

  //Expand functionality
  const [expand, setExpand] = useState(false);
  const handleClick = () => {
    setExpand(!expand);
  };

  //Calculating total of all reviews
  let totalRatingSum = 0;
  reviewData &&
    reviewData.map((item, idx) => {
      return (totalRatingSum += parseInt(item.rating));
    });
  let overallAvgRating = reviewData && totalRatingSum / reviewData.length;

  return (
    <div className='score-wrapper'>
      <h4 onClick={handleClick} className='score-header m-0 py-4'>
        Khooobsooorat Score{' '}
        <i
          className={cx('fa fa-caret-down ms-2', expand ? 'up' : null)}
          aria-hidden='true'
        ></i>
      </h4>
      <hr className='my-0' />
      <div className='score-stats row pb-4'>
        <div className='col-3 stars d-flex gap-4 align-items-center'>
          <Rating rating={reviewData && overallAvgRating} />
          <h5 className='date'>{reviewData && reviewData.length} Reviews</h5>
        </div>
        <div className='col-2 likes d-flex gap-2 align-items-center'>
          <i className='fa fa-heart' aria-hidden='true'></i>
          <h5 className='date'>{reviewData && overallAvgRating * 20}%</h5>
        </div>
        <div className='col-2 overall-rating d-flex gap-2 align-items-center'>
          <div className='rate-point d-flex gap-1 align-items-center'>
            <span>{reviewData && overallAvgRating.toFixed(1)}</span>
            <i className='fa fa-star' aria-hidden='true'></i>
          </div>
          <h5 className='date'>Reviews</h5>
        </div>
      </div>
      <div hidden={!expand} className='overview my-4 my-sm-5'>
        {featureData &&
          featureData.map((item, idx) => {
            return (
              <div key={idx} className='grid-item'>
                <progress
                  id='file'
                  className='me-4'
                  value={item.averageRating * 20}
                  max='100'
                />
                <label htmlFor='file'>{item.name}</label>
              </div>
            );
          })}
      </div>
      <FilterComponent />
      <div className='review-product d-flex gap-3 mt-2'>
        <button className='btn review-btn d-flex align-items-center'>
          <i className='fa fa-pencil me-2' aria-hidden='true'></i>Review this
          product
        </button>
        <p>
          Share your thoughts with other customers by sharing an{' '}
          <a href='#'>unbiased review</a>
        </p>
      </div>
    </div>
  );
};

export default ScoreComponent;
